.training {
	background-image: url(../img/training-bg.jpg);
	@include cover;
	height: 100%;
	color: #fff;
	text-align: center;
	padding: 2em 0;
	&__title {
		@include PTs;
		@include css-lock(25, 43, 480, 960);
		// font-size: 43px
		line-height: 1.6em; }
	&__date {
		@include H;
		color: #f2326b;
		@include css-lock(18, 24, 480, 960);
		// font-size: 24px
		line-height: 1.2em;
		margin-top: 0.8em; }
	&__about {
		@include css-lock(14, 16, 480, 960);
		// font-size: 16px
		line-height: 1.4em; }
	&__days {

		@include clr;
		text-align: center;
		vertical-align: top;
		.day {
			width: 220px;
			float: left;
			&:not(:last-child) {
				margin-right: 20px; } }
		@include r(939) {
			.day {
				width: 45%;
				float: none;
				display: inline-block;
				vertical-align: top;
				margin-top: 10px;
				&:not(:last-child) {
					margin-right: 0; } } }

		@include r(500) {
			.day {
				width: 85%; } } }
	&__button {
		@include css-lock(18, 23, 480, 960);
		// font-size: 23px
		line-height: em(42);
		margin-top: 1.8em;
		margin-bottom: 0.7em; } }

.day {
	margin-top: em(45);
	&__title {
		@include bcpin;
		@include H;
		@include css-lock(19, 24, 480, 960);
		// font-size: 24px
		line-height: 1.75em; }

	&__list {
		text-align: left;
		margin-top: 1.6em;
		@include r(500) {
			margin-top: 0.5em; } }
	&__item {
		position: relative;
		padding: em(10) 0;
		&:not(:last-child) {
			border-bottom: 1px solid rgba(78, 76, 85, 0.9); } }
	&__time {
		@include css-lock(14, 14, 480, 960);
		// font-size: 14px
		font-weight: 700;
		// line-height: em(20)
		position: absolute;
		left: 0;
		top: 50%;
		transform: translateY(-50%); }
	&__text {
		margin-left: 3.5em;
		@include css-lock(15, 16, 480, 960);
		// font-size: 16px
		line-height: em(20);
 } }		// line-height: 21px


.application-form {
	@include bcpin;
	text-align: center;
	@include r(926) {
		padding: 0.5em 0; } }
.application {
	input {
		width: 9.565em;
		display: inline-block;
		height: 59px;
		padding: 0 1em;
		margin: 1.8em 0;
		margin-right: 0.675em;
		@include H;
		font-size: 23px;
		@include css-lock(18,23,480,960);
		line-height: 2em;
		background-color: #ffffff;
		color: #000;
		@include placeholder-color(#7d7d7d);
		border-radius: 2em;

		@include r(926) {
			margin: 0.5em;
			width: 220px; } }
	input[type=submit] {
		border: 1px solid #ffffff;
		background-color: rgba(255, 6, 35, 0);
		color: #fff;
		transition: 0.5s;
		margin-right: 0;
		@include r(926) {
			margin: 0.5em; }
		&:hover {
			background-color: rgba(0, 0, 0, 0.25); } } }
